import { isStandardBrowserEnv } from '@dimjs/lang';
import { uriParse } from './uri-parse.js';

/**
 * @description
 * Determine if a URL shares the same origin as the current location
 *
 * @example
 * ``` ts
 * isSameOrigin('https://github.com/axios/axios', 'https://github.com/test');
 * // => true
 * isSameOrigin('https://github.com/axios/axios');
 * // => false
 * ```
 *
 * @param {String} requestURL The URL to test
 * @returns {boolean} True if URL shares the same origin, otherwise false
 *
 * @docsCategory preference/utils/uri
 * @codeFilter is-same-origin
 */
export const isSameOrigin = (requestURL: string, url?: string) => {
  const originURL = uriParse(
    url || (isStandardBrowserEnv() ? window.location.href : '')
  );
  const parsed = uriParse(requestURL);
  return (
    parsed.protocol === originURL.protocol && parsed.host === originURL.host
  );
};
